/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #181818;
  color: #FAF7FF;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Philosopher", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Philosopher", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FAF7FF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FAF7FF;
  font-size: 2rem;
}

@media (min-width: 768px) {
  .app-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.app-container .image {
  position: relative;
  background-image: url(../img/image_1.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 20rem;
}
@media (min-width: 768px) {
  .app-container .image {
    height: auto;
  }
}
.app-container .image::after {
  content: "";
  width: 100%;
  height: 50%;
  position: absolute;
  left: 0;
  bottom: 0;
  background-image: linear-gradient(to top, #181818, transparent);
}
@media (min-width: 768px) {
  .app-container .image::after {
    height: 100%;
    width: 100%;
    right: 0;
    top: 0;
    background-image: linear-gradient(to left, #181818, transparent);
  }
}
.app-container .app {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  padding: 3rem 0;
  width: 95%;
}
.app-container .app h1 {
  text-transform: uppercase;
}

.form-container {
  width: 95%;
}
@media (min-width: 1024px) {
  .form-container {
    width: 65%;
  }
}

.form {
  padding: 1rem;
  font-size: 1.8rem;
}

.field {
  display: flex;
  flex-direction: column;
}
.field label {
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 2.5rem;
}
.field input {
  background-color: #FAF7FF;
  border: none;
  border-radius: 0.5rem;
  padding: 1rem;
  outline: none;
  transition: background-color 0.1s ease-in-out;
}
.field input:focus {
  background-color: #D3FBD8;
}

.button {
  background-color: #8083FF;
  padding: 1.5rem 4rem;
  color: #FAF7FF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  border-radius: 0.5rem;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .button {
    width: auto;
  }
}
.button:hover {
  background-color: #4d51ff;
  cursor: pointer;
}

.account-options {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.account-options a {
  color: #8083FF;
  font-size: 1.8rem;
  font-weight: 700;
  text-decoration: dashed;
  transition: color 0.3s ease-in-out;
}
.account-options a:hover {
  color: #4d51ff;
}
.account-options p {
  font-size: 1.8rem;
  color: #FAF7FF;
}